import type { Metadata } from "next";
import { Inter } from "next/font/google";
import "./globals.css";
import QueryProvider from "@/providers/QueryProvider";
import NavbarWrapper from "@/components/NavbarWrapper"; // New wrapper to use hooks
import { ThemeProvider } from "@/components/ThemeProvider";
import { FilterProvider } from "@/context/FilterContext";

const inter = Inter({ subsets: ["latin"] });

export const metadata: Metadata = {
  title: "NSE Stock Analyzer",
  description: "Real-time NSE stock analysis and ranking",
};

export default function RootLayout({
  children,
}: Readonly<{
  children: React.ReactNode;
}>) {
  return (
    <html lang="en" suppressHydrationWarning>
      <body className={`${inter.className} min-h-screen bg-background text-foreground transition-colors duration-300`}>
        <QueryProvider>
          <ThemeProvider
            attribute="class"
            defaultTheme="dark"
            enableSystem
            disableTransitionOnChange
          >
            <FilterProvider>
              <NavbarWrapper />
              <main className="w-full min-h-screen px-4 py-6">
                {children}
              </main>
            </FilterProvider>
          </ThemeProvider>
        </QueryProvider>
      </body>
    </html>
  );
}
